import {
    GET_COLLECTION_LIST
} from '../types';

const initialState = {
    collection: {},
    collections: []
};

function collectionReducer(state = initialState, action) {
    switch (action.type) {
        case GET_COLLECTION_LIST:
            return {
                ...state,
                collections: action.payload
            }
        default:
            return state;
    }
}

export default collectionReducer;